@import './utils/mixins';

body {
  margin: 0;
  padding: 0;
}

.card {
  @include flex-center;

  height: 408px;
  width: 200px;
  padding: 0;
  padding-left: 17px;
  padding-right: 17px;
  background-color: #fff;

  &__img {
    margin-top: 33px;
    width: 160px;
  }

  &__title {
    @include product-title-font;

    margin-top: 40px;
  }

  &__product-code {
    @include product-code-font;
    @include flex-left;

    margin-top: 4px;
    color: #616070;
  }

  &__product-price {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-top: 24px;
  }

  &__price-text {
    @include product-code;

    color: #616070;
  }

  &__price {
    @include buy-button-font;
  }

  &__buy-button {
    @include buy-button-font;

    margin-top: 16px;
    width: 166px;
    height: 40px;
    border-radius: 5px;
    border: none;
    background-color: #00acdc;
    color: #fff;

    &:hover {
      background-color: #fff;
      color: #00acdc;
      border: 1px solid #00acdc;
    }
  }
}

.stars-area {
  @include product-code-font;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  margin-top: 16px;
}

.stars {
  display: flex;
  flex-direction: row;

  &__ranking {
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
    margin-right: 4px;
    width: 16px;
    height: 16px;

    &__ranking:nth-child(-n + 4) {
      background-image: url(../images/star-active.svg);
    }
  }
}
