@mixin product-title-font {
  font-family: Roboto, sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
}

@mixin product-code-font {
  font-family: Roboto, sans-serif;
  font-weight: 400;
  font-size: 10px;
  line-height: 14px;
}

@mixin product-code {
  font-family: Roboto, sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
}

@mixin buy-button-font {
  font-family: Roboto, sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
}

@mixin flex-center {
  display: flex;
  align-items: center;
  flex-direction: column;
  box-sizing: border-box;
}

@mixin flex-left {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
